.board {
  outline: 2px solid rgb(134, 154, 189);
}

.row {
  height: 35px;
}

.cell {
  width: 35px;
  height: 35px;
  outline: 1px solid rgb(134, 154, 189);
  display: inline-block;
}

.cell-green {
  background-color: rgb(185, 213, 46);
}

.cell-red {
  background-color: rgb(255, 89, 0);
}

.cell-purple {
  background-color: rgba(234, 68, 234, 0.812);
}
